.privacy-policy .container {
  max-width: 1000px;
  /*
  margin: 30px auto;
  */
  margin-top: 2rem;
  padding: 20px;
  background: #fff;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
}
.privacy-policy h1, h2 {
  color: #333;
}
.privacy-policy h1 {
  font-size: 2rem;
  margin-bottom: 0.5rem;
}
.privacy-policy h2 {
  font-size: 1.7rem;
  margin-top: 3rem;
}
.privacy-policy p {
  margin: 1rem 0;
  font-size: 1.5rem;
  line-height: 1.5;
}
.privacy-policy .contact {
  margin-top: 2rem;
  /*
  font-style: italic;
  */
}

.privacy-policy li {
  list-style-type: none;
  margin-bottom: 1rem;
  font-size: 1.5rem;
  line-height: 1.5;
}

